import React, { FC, ReactElement, useEffect, useState } from "react";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import Modal from "../../components/Modal/Modal";
import {
  Cohort__Interface,
  FC_GetCohorts,
  FC_Search_Available_Candidates,
  FC_UpdateSchoolTrainingRequest,
  InterfaceTrainingRequestSchools,
  TrainingInterface,
} from "../actions/training.settings.action";
import InputSelect from "../../components/Inputs/InputSelect";
import { DATE } from "../../utils/functions";

type UpdateSchoolNumberProps = {
  close: () => void;
  success: (data: InterfaceTrainingRequestSchools) => void;
  school: InterfaceTrainingRequestSchools;
  remaining: number;
  training: TrainingInterface;
};

const UpdateSchoolNumber: FC<UpdateSchoolNumberProps> = ({
  close,
  school,
  remaining,
  training,
  success,
}): ReactElement => {
  const [number, setNumber] = useState("");
  const [laoding, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [loadingCheckAvaliable, setLoadingCheckAvailable] =
    useState<boolean>(true);
  // const [availableTeachers, setAvailableTeachers] = useState<number>(0);
  const [trainingCohorts, setTrainingCohorts] = useState<
    Cohort__Interface[] | null
  >(null);
  const [selectedCohortId, setSelectedCohortId] = useState<string>("");

  useEffect(() => {
    setNumber(school.total + "");

    if (loadingCheckAvaliable) {
      // selectSchoolCheckAvailable();
      if (training) {
        // setAvailableTeachers(0);
        setLoadingCheckAvailable(true);
        FC_Search_Available_Candidates(
          {
            criteria: training.trainee_criteria,
            end_date: training.end_date + "",
            start_date: training.start_date + "",
            school_code: school.school_id + "",
            sub_area_id: training.sub_area_id + "",
            topic: training.topic_id + "",
            training_id: training.id + "",
          },
          (status, data, err) => {
            if (!status) setError(`Error: ${err}`);

            setLoadingCheckAvailable(false);
            // if (data) setAvailableTeachers(data.total);
          }
        );
      }
    }
    if (trainingCohorts === null) {
      setLoadingCheckAvailable(true);
      FC_GetCohorts(
        training.id,
        (status: boolean, data: Cohort__Interface[], error: string) => {
          if (status === true) {
            setTrainingCohorts(data);
            setLoadingCheckAvailable(false);
          }
          if (error !== "") {
            setError(error);
          }
        }
      );
    }
  }, [school, loadingCheckAvaliable, training, trainingCohorts]);

  // const selectSchoolCheckAvailable = () => {
  //   if (training) {
  //     setAvailableTeachers(0);
  //     setLoadingCheckAvailable(true);
  //     FC_Search_Available_Candidates(
  //       {
  //         criteria: training.trainee_criteria,
  //         end_date: training.end_date + "",
  //         start_date: training.start_date + "",
  //         school_code: school.school_id + "",
  //         sub_area_id: training.sub_area_id + "",
  //         topic: training.topic_id + "",
  //         training_id: training.id + "",
  //       },
  //       (status, data, err) => {
  //         if (!status) setError(`Error: ${err}`);

  //         setLoadingCheckAvailable(false);
  //         if (data) setAvailableTeachers(data.total);
  //       }
  //     );
  //   }
  // };

  const updateTotalNumber = () => {
    setError("");

    if (!Number.isInteger(+number))
      return setError("Please provide a valid number");

    if (+number <= 0) return setError("The total must not be less than zero");
    if (selectedCohortId === "") return setError("Please choose cohort");

    // if (number > availableTeachers)
    //   return setError(
    //     `The the maximum available candidates on this school is ${availableTeachers}`
    //   );

    // if (number > remaining)
    //   return setError(
    //     `The remaining candidates on this training is ${remaining}`
    //   );

    setLoading(true);
    FC_UpdateSchoolTrainingRequest(
      {
        training_id: training.id,
        total: number,
        school_id: school.school_code,
        district_id: school.district_id,
        cohort_id: selectedCohortId,
      },
      (status, msg) => {
        if (status) {
          success({ ...school, total: +number });
        } else {
          setError(`Error: ${msg}`);
          setLoading(false);
        }
      }
    );
  };

  return (
    <Modal
      backDrop={true}
      close={close}
      backDropClose={false}
      displayClose={true}
      padding={{
        title: true,
        body: undefined,
        footer: undefined,
      }}
      title={"Update school number"}
    >
      <div className="border-t p-4">
        {error !== "" && <Alert theme="danger">{error}</Alert>}
        {loadingCheckAvaliable ? (
          <div className="text-center">
            <section className="inline-block">
              <Loading />
            </section>

            <h2 className="text-lg pt-2">Checking available on school</h2>
          </div>
        ) : (
          <>
            <div className="border p-2 rounded">
              <label className="text-sm opacity-30">School</label>
              <h2 className="text-lg font-bold">
                {school.school_code} {school.school_name}
              </h2>
            </div>

            {trainingCohorts !== null && (
              <div className="py-2">
                <InputSelect
                  onChange={(event: React.ChangeEvent<HTMLSelectElement>) => {
                    setSelectedCohortId(event.target.value);
                    setError("");
                  }}
                  value={selectedCohortId}
                  error={""}
                  label={"Training Cohort"}
                  placeholder={"Training cohort"}
                  name={""}
                  data={[
                    {
                      title: "",
                      value: "",
                    },
                    ...trainingCohorts.map((cohort) => ({
                      title: `${cohort.cohort_name} start from ${DATE(
                        cohort.cohort_start_date
                      )}, ending on
                  ${DATE(cohort.cohort_end_date)}`,
                      value: cohort.cohort_id,
                    })),
                  ]}
                />
              </div>
            )}

            <div className="py-2">
              <TextInput
                onChange={(e) => {
                  setNumber(e.target.value);
                  setError("");
                }}
                value={number}
                error={""}
                type="text"
                label={"Set Teachers you need"}
                placeholder={"Set Teachers you need"}
                name={""}
                disabled={laoding}
              />
            </div>
            <div className="text-right">
              <Button
                type="button"
                loading={laoding}
                onClick={updateTotalNumber}
              >
                Update numbers
              </Button>
            </div>
          </>
        )}
      </div>
    </Modal>
  );
};
export default UpdateSchoolNumber;
