import { FC, useEffect, useState } from "react";
import {
  //   CohortSession__Interface,
  Cohort__Interface,
  FC_GetCohorts,
} from "../../actions";
import { formatDate } from "../../utils/functions";

const CohortDetails: FC<{
  training_id: string;
}> = ({ training_id }) => {
  const [selectedCohort, setSelectedCohort] = useState<string | null>(null);
  const [cohorts, setCohorts] = useState<Cohort__Interface[]>([]);
  const selectCohortHandler = (cohortId: string) => {
    if (cohortId === selectedCohort) {
      setSelectedCohort(null);
    } else {
      setSelectedCohort(cohortId);
    }
  };

  useEffect(() => {
    FC_GetCohorts(training_id, (status, data, err) => {
      setCohorts(data);
    });
  }, [training_id]);

  return (
    <div>
      <div>
        <table className="w-full text-left">
          <thead>
            <tr>
              <th className="border p-2">#</th>
              <th className="border p-2">Name</th>
              <th className="border p-2">Sessions</th>
              <th className="border p-2">Start date</th>
              <th className="border p-2">End date</th>
              <th className="border p-2"></th>
            </tr>
          </thead>
          <tbody>
            {cohorts.map((cohort, i) => (
              <>
                <tr
                  key={i}
                  className={` border p-2
                  ${selectedCohort === cohort.cohort_id ? "" : ""} `}
                  onClick={selectCohortHandler.bind(this, cohort.cohort_id)}
                >
                  <td className="border p-2">{i + 1}</td>
                  <td className="border p-2">{cohort.cohort_name}</td>
                  <td className="border p-2">
                    {cohort.cohort_sessions.length}
                  </td>
                  <td className="border p-2">{cohort.cohort_start_date}</td>
                  <td className="border p-2">{cohort.cohort_end_date}</td>
                  <td className="border p-2">{i}</td>
                </tr>
                {selectedCohort === cohort.cohort_id && (
                  <tr>
                    <td colSpan={9} className="py-2">
                      Sessions:
                      <div className="grid gap-3 grid-cols-5">
                        {cohort.cohort_sessions.map((el, i) => (
                          <div
                            key={i}
                            className="bg-gray-200 text-center py-3 w-32 h-32 flex items-center"
                          >
                            <div>
                              <div className=""></div>
                              <section>
                                <h4 className="text-2xl font-bold">
                                  {new Date(el.date).getDate()}
                                </h4>
                              </section>
                            </div>
                            <p>{formatDate(el.date)}</p>
                          </div>
                        ))}
                      </div>
                    </td>
                  </tr>
                )}
              </>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default CohortDetails;
